import axios from 'axios'
import { Notice } from 'view-design'
import Cookies from 'js-cookie'
import { Base64 } from 'js-base64'
import store from '@/store'
import { replaceParamVal, setToken, clearToken } from '@/libs/util'
import config from '@/config'
const { isHttps } = config

class HttpRequest {
    constructor(baseUrl = baseURL) {
        this.baseUrl = baseUrl
        this.queue = {}
    }
    getInsideConfig() {
        const config = {
            baseURL: this.baseUrl,
            headers: {
                'X-Xsrftoken': Cookies.get('_xsrf'),
                'appcode': 'job',
                'tenantid': store.state.app.theBusiness,
                'tenant': Base64.encode(store.state.app.resource_group)
            }
        }
        return config
    }
    destroy(url) {
        delete this.queue[url]
        if (!Object.keys(this.queue).length) {
            // Spin.hide()
        }
    }
    interceptors(instance, url) {
        // 
        instance.interceptors.request.use(config => {
                // loading...
                if (!Object.keys(this.queue).length) {
                    // Spin.show() // 
                }
                this.queue[url] = true
                return config
            }, error => {
                return Promise.reject(error)
            })
            // 
        instance.interceptors.response.use(res => {
            this.destroy(url)
            const { data, status, request } = res
            return { data, status, request }
        }, error => {
            this.destroy(url)
            if (error.response.status === '401' || error.response.status === 401) {
                clearToken()
                location.reload()
            } else if (error.response.status === 402) {
                console.log(error.response)
                Notice.warning({
                    title: ''
                })
            } else if (error.response.status === 403) {
                console.log(error.response)
                Notice.warning({
                    title: '',
                    desc: `${error.response.data}`
                })
            } else if (error.response.status === 500) {
                Notice.error({
                    title: '',
                    desc: `${error.response.data}`
                })
            }
            return Promise.reject(error)
        })
    }
    request(options) {
        const instance = axios.create()
        options = Object.assign(this.getInsideConfig(), options)
        this.interceptors(instance, options.url)
        return instance(options)
    }
    get(options) {
        const instance = axios.create()
        options = Object.assign(this.getInsideConfig(), options)
        this.interceptors(instance, options.url)
        return instance(options)
    }
}
export default HttpRequest
