<template>
  <div v-if="showBusiness" class="busines-avatar-dropdown">
    <template v-if="theBusiness">
      <Select filterable label-in-value v-model="theBusiness" style="width:180px" placeholder='' @on-change="handlerChange">
        <Option v-for="item in businessData" :value="item.business_id" :key="item.business_id" :label="item.resource_group" />
      </Select>
    </template>
  </div>
</template>

<script>
import { mapActions, mapState, mapMutations } from 'vuex'
export default {
  name: 'businessSelect',
  data () {
    return {
      businessData: [],
      theBusiness: this.$store.state.app.theBusiness,
    }
  },
  methods: {
    ...mapActions(['handlerGetBusinessList', 'handlerSelectBusiness']),
    ...mapMutations(['setBusinessObj']),
    handlergetBusinessList () {
      this.handlerGetBusinessList().then(res => {
        if (res.data.result && res.data.data.length > 0) {
          this.businessData = res.data.data
          if (!this.theBusiness) {
            this.handlerChange1(res.data.the_biz_map)
          }
        } else {
          this.businessData = []
          this.$Message.error({ content: res.data.msg, duration: 5 })
        }
      })
    },
    handlerChange1 (value) {
      this.handlerSelectBusiness(value).then(res => {
        if (!res.data.result) {
          this.$Message.error({ content: res.data.msg, duration: 5 })
        } else {
          let currentBusiness = this.businessData.filter(item => item.business_id == res.data.data.value)
          currentBusiness.length > 0 && this.setBusinessObj(currentBusiness[0])
        }
      })
    },
    handlerChange (value) {
      this.handlerSelectBusiness({ 'business_id': value.value, 'resource_group': value.label }).then(res => {
        if (!res.data.result) {
          this.$Message.error({ content: res.data.msg, duration: 5 })
        } else {
          let currentBusiness = this.businessData.filter(item => item.business_id == value.value)
          currentBusiness.length > 0 && this.setBusinessObj(currentBusiness[0])
        }
      })
    },
  },
  computed: {
    ...mapState({
      rules: state => state.user.rules,
      showBusiness: state => state.app.showBusiness,
      theBusinessStore: state => state.app.theBusiness,
    })
  },
  watch: {
    theBusinessStore (val) {
      this.theBusiness = val
    },
    theBusiness (val) {
    }
  },
  created () {
    this.handlergetBusinessList()
  },
  mounted () {
    // document.addEventListener("visibilitychange", function () {
    //   if (document.visibilityState == "visible") {
    //     console.log('visible', store.state.app.theBusiness)
    //   }
    // });
  }
}
</script>

