<template>
  <div v-if="QIANKUN" id="app-qk">
    <router-view />
  </div>
  <div v-else id="app">
    <router-view />
  </div>
</template>

<script>
import { mapActions, mapMutations } from 'vuex'
import actions from "./shared/actions";
export default {
  name: "App",
  data () {
    return {
      environment: "",
      QIANKUN: window.__POWERED_BY_QIANKUN__ === undefined ? false : true
    };
  },
  mounted () {
    if (this.QIANKUN) {
      actions.onGlobalStateChange((state) => {
        this.getInfo(state);
      }, true);
    }
  },
  methods: {
    ...mapActions(["handlerGetBusinessList", "handlerSelectBusiness"]),
    ...mapMutations(['setBusiness', 'setResourceGroup']),
    async getInfo (token) {
      this.environment = token;
    },
  },
  watch: {
    //  `question` 
    environment (newValue, oldValue) {
      this.$store.state.app.businessData = { ...newValue.businessData }
      // let businessData = { ...newValue.businessData }
      // this.handlerSelectBusiness({ business_id: newValue.value, resource_group: newValue.name, businessData: businessData }).then((res) => {
      //   console.log('handlerSelectBusiness environment', res.data);
      // });
      this.setBusiness(newValue.value)
      this.setResourceGroup(newValue.name)
      console.log(newValue.name, newValue.value, oldValue === newValue);
    },
  },
};
</script>

