import Vue from 'vue'
import Router from 'vue-router'
import { routes } from './routers'
import store from '@/store'
// import ViewUI from 'view-design'
import { getToken, getISLOGIN, setTitle, setToken, clearToken } from '@/libs/util'
import config from '@/config'
const { homeName } = config

Vue.use(Router)
const router = new Router({
    routes
})

router.beforeEach((to, from, next) => {
    to.meta.title && setTitle(to.meta.title)
    const is_login = getISLOGIN()
    const login_route = {
        name: 'login',
        params: {
            "c_url": window.location.href
        }
    }

    //  
    const cusLogin = () => {
        if (config.ssoLogin) {
            next(false)
            window.location.href = config.ssoLogin
        } else {
            next(login_route)
        }
    }

    if (is_login) {
        if (!store.state.router.hasGetRules) {
            store.dispatch('authorization').then(rules => {
                store.dispatch('concatRoutes', rules).then(routers => {
                    router.addRoutes(routers)
                    next({...to, replace: true })
                }).catch((err) => {
                    setToken('')
                    clearToken()
                    cusLogin()
                })
            }).catch((err1) => {
                setToken('')
                clearToken()
                cusLogin()
            })
        } else {
            next()
        }
    } else {
        if (to.name === 'login' && !config.ssoLogin) next()
        else cusLogin()
    }
})


router.afterEach(to => {
    window.scrollTo(0, 0)
})

export default router