const bpmn = {
    state: {
        nodeVisible: false,
        nodeInfo: {},
        removeEle: null
    },
    mutations: {
        TOGGLENODEVISIBLE: (state, visible) => {
            state.nodeVisible = visible;
        },
        SETNODEINFO: (state, info) => {
            state.nodeInfo = info;
        },
        RemoveEle: (state, ele) => {
            state.removeEle = ele;
        }
    },
    actions: {}
};

export default bpmn;