import {
  login,
  logout,
  getUserInfo,
  getMessage,
  getContentByMsgId,
  hasRead,
  removeReaded,
  restoreTrash,
  Authorization,
  getUnreadCount
} from '@/api/user'
import { setToken, getToken, clearToken } from '@/libs/util'
import loveJpg from '@/assets/images/love.jpg'

export default {
  state: {
    userName: '',
    nickName: '',
    userId: '',
    avatarImgPath: loveJpg,
    token: '123456',
    access: '',
    rules: {},
    hasGetInfo: false,
    unreadCount: 0,
    messageUnreadList: [],
    messageReadedList: [],
    messageTrashList: [],
    messageContentStore: {}
  },
  mutations: {
    setAvatar (state, avatarPath) {
      if (avatarPath) {
        state.avatarImgPath = avatarPath
      }
      // state.avatarImgPath = avatarPath
    },
    setUserId (state, id) {
      state.userId = id
    },
    setUserName (state, name) {
      state.userName = name
    },
    setNickName (state, name) {
      state.nickName = name
    },
    setAccess (state, access) {
      state.access = access
    },
    setToken (state, token) {
      state.token = token
      setToken(token)
    },
    clearToken () {
      clearToken()
    },
    setHasGetInfo (state, status) {
      state.hasGetInfo = status
    },
    setMessageCount (state, count) {
      state.unreadCount = count
    },
    setMessageUnreadList (state, list) {
      state.messageUnreadList = list
    },
    setMessageReadedList (state, list) {
      state.messageReadedList = list
    },
    setMessageTrashList (state, list) {
      state.messageTrashList = list
    },
    updateMessageContentStore (state, { msg_id, content }) {
      state.messageContentStore[msg_id] = content
    },
    moveMsg (state, { from, to, msg_id }) {
      const index = state[from].findIndex(_ => _.msg_id === msg_id)
      const msgItem = state[from].splice(index, 1)[0]
      msgItem.loading = false
      state[to].unshift(msgItem)
    },
    SET_RULES (state, rules) {
      state.rules = rules
    }
  },
  getters: {
    messageUnreadCount: state => state.messageUnreadList.length,
    messageReadedCount: state => state.messageReadedList.length,
    messageTrashCount: state => state.messageTrashList.length
  },
  actions: {
    // 
    handleLogin ({ commit }, { username, password, dynamic, c_url }) {
      username = username.trim()
      password = password.trim()
      password = window.btoa(window.btoa(password))
      return new Promise((resolve, reject) => {
        login({ username, password, dynamic, c_url }).then(res => {
          const data = res.data
          if (data.code === 0) {
            commit('setToken', data.auth_key)
            commit('setUserName', data.username)
            commit('setNickName', data.nickname)
            commit('setAvatar', data.setAvatar)
            // setAvatar
          }
          resolve(data)
        }).catch(err => {
          reject(err)
        })
      })
    },
    // handleLogin({ commit }, { userName, password }) {
    //     userName = userName.trim()
    //     return new Promise((resolve, reject) => {
    //         login({ userName, password }).then(res => {
    //             const data = res.data
    //             commit('setToken', data.token)
    //             commit('setUserName', data.username)
    //             commit('setNickName', data.nickname)
    //             resolve()
    //         }).catch(err => {
    //             reject(err)
    //         })
    //     })
    // },
    // 
    // handleLogOut({ state, commit }) {
    //     console.info('handleLogOut')
    //     return new Promise((resolve, reject) => {
    //         logout(state.token).then(() => {
    //             commit('setToken', '')
    //             commit('setAccess', [])
    //             resolve()
    //         }).catch(err => {
    //             reject(err)
    //         })
    //     })
    // },
    handleLogOut ({ state, commit }) {
      return new Promise((resolve, reject) => {
        commit('setToken', '')
        commit('clearToken', '')
        commit('setAccess', [])
        resolve()
      })
    },
    //
    authorization ({ commit }, token) {
      return new Promise((resolve, reject) => {
        Authorization().then(res => {
          if (parseInt(res.status) === 401) {
            commit('setToken', '')
            commit('setAccess', [])
            reject(new Error('token error'))
          } else {
            const data = res.data.data
            resolve(data.rules.page)
            //
            commit('setUserName', data.username)
            commit('setNickName', data.nickname)
            if (data.avatar) {
              commit('setAvatar', data.avatar)
            }
            commit('SET_RULES', data.rules.component)
          }
        }).catch(error => {
          // console.log('error----------', JSON.parse(JSON.stringify(error)))
          reject(error)
        })
      })
    },
    // 
    getUserInfo ({ state, commit }) {
      return new Promise((resolve, reject) => {
        try {
          getUserInfo(state.token).then(res => {
            const data = res.data
            commit('setAvatar', data.avatar)
            commit('setUserName', data.name)
            commit('setUserId', data.user_id)
            commit('setAccess', data.access)
            commit('setHasGetInfo', true)
            resolve(data)
          }).catch(err => {
            reject(err)
          })
        } catch (error) {
          reject(error)
        }
      })
    },
    // 
    getUnreadMessageCount ({ state, commit }) {
      getUnreadCount().then(res => {
        const { data } = res
        commit('setMessageCount', data)
      })
    },
    // 
    getMessageList ({ state, commit }) {
      return new Promise((resolve, reject) => {
        getMessage().then(res => {
          const { unread, readed, trash } = res.data
          commit('setMessageUnreadList', unread.sort((a, b) => new Date(b.create_time) - new Date(a.create_time)))
          commit('setMessageReadedList', readed.map(_ => {
            _.loading = false
            return _
          }).sort((a, b) => new Date(b.create_time) - new Date(a.create_time)))
          commit('setMessageTrashList', trash.map(_ => {
            _.loading = false
            return _
          }).sort((a, b) => new Date(b.create_time) - new Date(a.create_time)))
          resolve()
        }).catch(error => {
          reject(error)
        })
      })
    },
    // id
    getContentByMsgId ({ state, commit }, { msg_id }) {
      return new Promise((resolve, reject) => {
        let contentItem = state.messageContentStore[msg_id]
        if (contentItem) {
          resolve(contentItem)
        } else {
          getContentByMsgId(msg_id).then(res => {
            const content = res.data
            commit('updateMessageContentStore', { msg_id, content })
            resolve(content)
          })
        }
      })
    },
    // 
    hasRead ({ state, commit }, { msg_id }) {
      return new Promise((resolve, reject) => {
        hasRead(msg_id).then(() => {
          commit('moveMsg', {
            from: 'messageUnreadList',
            to: 'messageReadedList',
            msg_id
          })
          commit('setMessageCount', state.unreadCount - 1)
          resolve()
        }).catch(error => {
          reject(error)
        })
      })
    },
    // 
    removeReaded ({ commit }, { msg_id }) {
      return new Promise((resolve, reject) => {
        removeReaded(msg_id).then(() => {
          commit('moveMsg', {
            from: 'messageReadedList',
            to: 'messageTrashList',
            msg_id
          })
          resolve()
        }).catch(error => {
          reject(error)
        })
      })
    },
    // 
    restoreTrash ({ commit }, { msg_id }) {
      return new Promise((resolve, reject) => {
        restoreTrash(msg_id).then(() => {
          commit('moveMsg', {
            from: 'messageTrashList',
            to: 'messageReadedList',
            msg_id
          })
          resolve()
        }).catch(error => {
          reject(error)
        })
      })
    }
  }
}
